.error-text{
    width: 40%;
    color: #f00;
    margin-top: 0.5rem;
    background-color: #fff;
   }

   /* css for bottom sticking footer */
footer > div {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  footer {
    z-index: 2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    background-color: #fff;
    left: 0;
    bottom: 0;
    width: 100%;
    overflow: hidden;
  }

  footer > button{
      height: 100%;
  }